// ================ 计算属性与函数工具 =================
const maxTrendValue = computed(() => Math.max(...trendData.value.map((item) => item.value)));
const minTrendValue = computed(() => Math.min(...trendData.value.map((item) => item.value)));
/**
 * 根据油耗值和位置计算折线点的绝对定位样式
 * @param value 当前点的油耗值
 * @param index 当前下标
 */
const getPointStyle = (value, index) => {
    // 避免除以 0，这里设置一个默认区间
    const safeRange = Math.max(maxTrendValue.value - minTrendValue.value, 1);
    // 线条会占据容器 80% 的高度，上下留有 10% 的内边距保证视觉呼吸
    const heightPercent = ((value - minTrendValue.value) / safeRange) * 80 + 10;
    const leftPercent = trendData.value.length === 1
        ? 50
        : (index / (trendData.value.length - 1)) * 100;
    return `left:${leftPercent}%;bottom:${heightPercent}%`;
};
/**
 * 统一处理页面跳转或后续功能入口
 * @param actionKey 操作 key
 */
const handleNavigate = (actionKey) => {
    uni.showToast({
        title: `功能 ${actionKey} 开发中`,
        icon: 'none'
    });
};
/**
 * 底部操作栏点击事件，根据 key 做简单分类
 */
const handleAction = (action) => {
    if (action.key === 'add') {
        handleNavigate('addRecord');
        return;
    }
    handleNavigate(action.key);
};
